.guarantees-section{
    padding-bottom: 40px;
    background-color: #F1F1F1;
}

.guarantees__title{
margin-bottom: 40px;
font-weight: 800;
font-size: 26px;
line-height: 1.3;
text-align: center;
letter-spacing: 0.03em;
color: var(--main-black-color);
}

.guarantees-container{
    padding-top: 40px;
    padding-bottom: 40px;
}

.guarantees-list{
    position: relative;
}

.guarantees-list__item{
padding-left: 24px;
font-weight: 500;
font-size: 12px;
line-height: 1.6;
color: var(--additional-text-color);
&:before{
    display: inline-flex;
    position: absolute;
    content: " ";
    width: 17px;
    height: 20px;
    left: -7px;
    background-size: contain;
    background-repeat: no-repeat;
    background-image: url("../images/guarantees/Fire.svg");
}
&:not(:last-child){
    border-left: 1px dashed var(--secondary-text-color);
}
}


.guarantees-list__item + .guarantees-list__item{
    &:not(:first-child){
        margin-top: 20px;
    }
    }

    .symbol,
    .result-symbol{
        color: var(--accent-color);
        }